import React, { useEffect, useState } from "react";
import Header from "./components/Header";
const Gallery = (props) => {
    const activeQuery = window.location.hash
     const params = activeQuery.split('/');
    const activeBranch = params[2];
    const pageQuery = params[3];
    const idQuery = params[4]
    const pageSet = pageQuery?pageQuery.split('?')[1].split(';'):null;
    if(pageSet?(pageSet[0].split('=')[0]!=="page"):false){
        window.location.hash = '#/404'
    }
    const pageNumber = pageSet?pageSet[0].split('=')[1]:null;
    const order = pageSet?pageSet[1]:null;
    if(order!=="asc"&& order!=="desc"){
        window.location.hash = '#/404'
    }

    const [link, setLink] = useState(order);
    const goToSearchQuery = event => {
        event.preventDefault();
        const { className, name } = event.target
        window.location.hash = name;
    }
    const changeOrderQuery = event => {
        const {value} = event.target;
        if(order!==value){
            const linkToGoTo = activeQuery.split(';')[0] + ';' + value;
            setLink(value);
            window.location.hash = linkToGoTo;
            window.location.reload();
        }
    }
    return <>
        <Header bool={props} parent="gallery"/>
        <div className="gallery-screen">
            <div className="gallery-search-browser">
                    <div className="gallery-browser-header">
                        <div className="gallery-button-choices">
                            <button onClick={goToSearchQuery} name="#/gallery/digital-art/q?page=1;asc" className="gallery-link" id="to-digital-art-link">{props?"Digital art":"Дигитални картини"}</button>
                            <button onClick={goToSearchQuery} name="#/gallery/traditional-art/q?page=1;asc" className="gallery-link" id="to-traditional-art-link">{props?"Traditional art":"Традиционни картини"}</button>
                            <button onClick={goToSearchQuery} name="#/gallery/3dmodels/q?page=1;asc" className="gallery-link" id="to-3dmodels-link">{props?"3D models":"3D модели"}</button>
                            <button onClick={goToSearchQuery} name="#/gallery/animations/q?page=1;asc" className="gallery-link" id="to-animations-link">{props?"Animations":"Анимации"}</button>
                            <button onClick={goToSearchQuery} name="#/gallery/manga/q?page=1;asc" className="gallery-link" id="to-manga-link">{props?"Manga":"Манга"}</button>
                            <button onClick={goToSearchQuery} name="#/gallery/apps-and-software/q?page=1;asc" className="gallery-link" id="to-apps-link">{props?"Apps and software":"Приложения и програми"}</button>
                        </div>
                        <div className="option-1">
                                <input type="radio" onChange={changeOrderQuery} id="ascending" className="radio-button-choices" checked={link==="asc"} name="order" value="asc"/>
                                <label htmlFor="ascending" className="radio-label">{props?"Oldest":"Стари"}</label>
                            </div>
                            <div className="option-2">
                                <input type="radio" onChange={changeOrderQuery} id="descending" checked={link==="desc"} className="radio-button-choices" name="order" value="desc"/>
                                <label className="radio-label2" htmlFor="descending">{props?"Newest":"Нови"}</label>
                            </div>
                    </div>
                    <div className="gallery-browser-body">
                        <a href="/#" className="lol">{activeQuery}</a>
                    </div>
            </div>
        </div>
    </>
}

export default Gallery;